fillPage();
var canvasStage,canvasLayer,canvasAnim,animItem=[],canvasOld,canvasNew;//canvas相关参数
var handleTimer=null;//节流定时器
$(window).on('resize',function(){
    clearTimeout(handleTimer);
    handleTimer=setTimeout(function(){
        globalVar.winW=window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        isDrawCavas();
        if(canvasOld!=canvasNew){
            if(canvasNew=='pc'){
                drawCanvas();
            };
            if(canvasNew=='mobile'){
                drawMobileCanvas();
            };
        };
        if(globalVar.svg){
            svgResponse();
        };
    },300);
});
//内容响应
function fillPage(){
    var pacManImgO=$("#pacManImg");
    var bearcatImgO=$("#bearcatImg");
    var bearcatSvgO=$("#bearcat-warrior");
    if(!isDrawCavas()){//不支持canvas的就直接用图片替代位置
        replaceHtml(pacManImgO);
    }else{
        if(canvasNew=='pc'){
            drawCanvas();
        };
        if(canvasNew=='mobile'){
            drawMobileCanvas();
        };
    };
    //浏览器是否支持svg
    if(globalVar.svg){
        pacManImgO.remove();
        svgResponse();
        bearCatAn();
    }else{
        bearcatSvgO.remove();
        replaceHtml(bearcatImgO);
    };
};
//svg响应
function svgResponse(){
    var svgO=$('#bearcat-warrior'),
        svgOP=svgO.parent(),
        svgOPW=svgOP.width();
    if(svgO) {
        svgO.css({'width':'100%','height':svgOPW+'px'});
    };
};
//判断是走移动，还是走PCcanvas;
function isDrawCavas(){
    if(globalVar.canvas){
        if((globalVar.mobile && globalVar.winW<=780) || globalVar.winW<=780){
            canvasNew='mobile';
        }else{
            canvasNew='pc';
        }
        return true;
    }
    return false;

};
//执行html替换
function replaceHtml(obj){
    obj.replaceWith(obj.attr('data-html'));
};
//给制canvas
function drawCanvas(){
    canvasOld=canvasNew='pc';
    destoryItem();
    animItem=[];
    canvasStage=new Konva.Stage({
        container:'pacManCanvas',
        width:1600,
        height:700
    });
    var animations={
        cyanMon:[0,0,40,40,40,0,40,40],
        redMon:[0,40,40,40,40,40,40,40],
        beansMan:[0,80,40,42,42,80,40,42,85,80,43,42]

    };
    var dotArr=[];
    //建立canvas层
    canvasLayer=new Konva.Layer({
        hitGraphEnabled:false
    });
    //添加物体,先绘背景，再绘物件
    drawImg.addImg('../images/gameCenter/game-center-canvas-bg.jpg',0,0,1600,700,canvasStage,canvasLayer,function(){
        drawImg.addImg('../images/gameCenter/apple.png',178,90,34,34,canvasStage,canvasLayer,aniBack,'apple');//绘制苹果fruitCb
        drawImg.addImg('../images/gameCenter/berries.png',1186,570,42,42,canvasStage,canvasLayer,aniBack,'berries');//绘制草莓fruitCb
        drawImg.addSprite('../images/gameCenter/game-center-sprite.png',1189,129,animations,'cyanMon',canvasStage,canvasLayer,function(obj){
            aniBack(obj,128,296);
        },'cyanMon');//给制青怪
        drawImg.addSprite('../images/gameCenter/game-center-sprite.png',372,367,animations,'redMon',canvasStage,canvasLayer,function(obj){
            aniBack(obj,367,534);
        },'redMon');//给制红怪
        drawImg.addSprite('../images/gameCenter/game-center-sprite.png',501,329,animations,'beansMan',canvasStage,canvasLayer,function(obj){
            aniBack(obj,501,1055);
        },'beansMan',6);//绘制豆人beansCb
    });
    //绘制回调
    function aniBack(obj,reset,end){
        var fruitSign=true;
        var count=0;
        var apple,berries,cyanMon,cyanNow,cyanSign,cyanReset,cyanEnd,redMon,redNow,redSign,redReset,redEnd,beansMan,beansSped,beansReset,beansEnd,beansNow;
        //对于那些需要做位移的元素存储它的临界点
        if(reset) obj['resetPo']=reset;
        if(end) obj['endPo']=end;
        animItem.push(obj);
        //待到所有元素加载完成执行动画
        if(animItem.length==5){
            //获取元素
            apple=canvasStage.find('#apple')[0];
            berries=canvasStage.find('#berries')[0];
            cyanMon=canvasStage.find('#cyanMon')[0];
            redMon=canvasStage.find('#redMon')[0];
            beansMan=canvasStage.find('#beansMan')[0];
            cyanReset=cyanMon['resetPo'];
            cyanEnd=cyanMon['endPo'];
            cyanNow=cyanReset;
            cyanSign=true;
            redReset=redMon['resetPo'];
            redEnd=redMon['endPo'];
            redNow=redReset;
            redSign=true;
            beansSped=1;
            beansReset=beansMan['resetPo'];
            beansEnd=beansMan['endPo'];
            beansNow=beansReset;
            canvasAnim = new Konva.Animation(function(frame) {
                count++;
                if(count%20==0){
                    fruitSign=!fruitSign;
                }
                //水果的动效
                if(fruitSign){
                    apple.scale({ x :1.05, y : 1.05});
                    berries.scale({ x :1, y : 1});
                }else{
                    apple.scale({ x :1, y : 1});
                    berries.scale({ x :1.05, y : 1.05});
                }
                //青怪物我动效
                if(cyanNow<=cyanReset){
                    cyanSign=true;
                };
                if(cyanNow>cyanEnd){
                    cyanSign=false;
                };
                if(cyanSign){
                    cyanNow++;
                }else{
                    cyanNow--;
                }
                //红怪物我动效
                if(redNow<=redReset){
                    redSign=true;
                };
                if(redNow>redEnd){
                    redSign=false;
                };
                if(redSign){
                    redNow++;
                }else{
                    redNow--;
                }
                //主豆人动效
                beansNow+=beansSped;
                clearDot(beansNow);
                if(beansNow>beansEnd){
                    clearDotArr();
                    draWPoint();
                    beansNow=beansReset;
                };
                beansMan.setX(beansNow);
                cyanMon.setY(cyanNow);
                redMon.setY(redNow);
            }, canvasLayer);
            draWPoint();
            canvasAnim.start();
        }
    }
    //绘制20个豆豆
    function draWPoint(){
        var dotY=348,dotX=560,diffX=20,dotR=3;
        for(var i=0;i<20;i++){
            drawImg.drawDot(dotX+i*(diffX+dotR*2),dotY,dotR,'#fff9b9',canvasStage,canvasLayer,dotArr);
        }
    }
    //清除豆豆数组
    function clearDotArr(){
        dotArr.length=0;
    }
    //消除豆豆
    function clearDot(x){
        for(var i=0,len=dotArr.length;i<len;i++){
            if(dotArr[i].attrs.x<=x+38){
                dotArr[i].destroy();
            }
        }
    }
};
